import React, { useState } from 'react';
import { Form, Row, Col, InputGroup, Button } from 'react-bootstrap';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';

const DateDifferenceCalculator: React.FC = () => {
    // State for the start and end dates
    const [startDate, setStartDate] = useState<Date | null>(null);
    const [endDate, setEndDate] = useState<Date | null>(null);

    // Function to calculate the difference in hours
    const calculateDifferenceInHours = () => {
        if (startDate && endDate) {
            const diffInMs = endDate.getTime() - startDate.getTime();
            return (diffInMs / (1000 * 60 * 60)).toFixed(2); // Convert to hours and fix to 2 decimal places
        }
        return '0.00'; // Fallback if one of the dates is not selected
    };

    // Function to reset both start and end dates
    const resetDates = () => {
        setStartDate(null);
        setEndDate(null);
    };

    return (
        <div className="p-4">
            <h3>Date Difference Calculator</h3>
            <Form>
                <Row className="mb-3">
                    {/* Start Date Picker */}
                    <Form.Group as={Col} controlId="formStartDate">
                        <Form.Label>Start Date:</Form.Label>
                        <DatePicker
                            selected={startDate}
                            onChange={(date: Date | null) => setStartDate(date)}
                            showTimeSelect
                            dateFormat="Pp"
                            className="form-control" // Use bootstrap styling
                            placeholderText="Select start date"
                        />
                    </Form.Group>

                    {/* End Date Picker */}
                    <Form.Group as={Col} controlId="formEndDate">
                        <Form.Label>End Date:</Form.Label>
                        <DatePicker
                            selected={endDate}
                            onChange={(date: Date | null) => setEndDate(date)}
                            showTimeSelect
                            dateFormat="Pp"
                            className="form-control" // Use bootstrap styling
                            placeholderText="Select end date"
                        />
                    </Form.Group>
                </Row>
            </Form>

            {/* Result Section */}
            <InputGroup className="mt-3">
                <InputGroup.Text>Result:</InputGroup.Text>
                <Form.Control
                    type="text"
                    readOnly
                    value={`${calculateDifferenceInHours()} hours`}
                />
            </InputGroup>

            {/* Reset Button */}
            <div className="mt-3">
                <Button variant="secondary" onClick={resetDates}>
                    Reset
                </Button>
            </div>
        </div>
    );
};

export default DateDifferenceCalculator;
